/* Border Radius */
.radius-5 {
  border-radius: 5px;
}

.radius-10 {
  border-radius: 10px;
}

/* Box Shadow */
.box-shadow {
  box-shadow: 0 5px 40px rgba(0, 0, 0, 0.05);
}

.no-shadow {
  box-shadow: none;
}

/* List Style */
.list-inline {
  margin-bottom: 0;
}

.list-inline li {
  display: inline-block;
}

.list-check {
  margin-bottom: 0;
  padding: 0;
  list-style: none;
}

.list-check li {
  font-size: 18px;
  line-height: 1.67;
  padding-left: 35px;
  position: relative;
}

.list-check li:not(:last-child) {
  margin-bottom: 20px;
}

.list-check li:after {
  position: absolute;
  font-family: 'FontAwesome';
  content: "";
  line-height: inherit;
  left: 5px;
  top: -2px;
}

.list-check li:before {
  position: absolute;
  left: 0;
  top: 5px;
  content: '';
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #FF988D;
  z-index: -1;
  opacity: .2;
}

.social_icon_list {
  padding: 0;
  margin: 0;
  list-style: none;
  line-height: 1;
}

.social_icon_list li {
  display: inline-block;
}

.social_icon_list li:not(:last-child) {
  margin-right: 25px;
}

.social_icon_list li a {
  font-size: 16px;
}

/* Owl Dots */
.owl-carousel .owl-dots {
  position: absolute;
  right: 50px;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
}

.owl-carousel button.owl-dot {
  display: inline-block;
  height: 3px;
  width: 16px;
}

.owl-carousel button.owl-dot.active {
  width: 28px;
}

/* Theme Input Group */
.theme-input-group {
  position: relative;
  width: 100%;
}

.theme-input-group input {
  width: 100%;
  height: 50px;
  border: none;
  padding-left: 15px;
  padding-right: 50px;
  color: #ffffff;
  background-color: #334E95;
}

.theme-input-group button {
  position: absolute;
  top: 4px;
  right: 4px;
  height: calc(100% - 8px);
  padding: 9px 28px;
  line-height: 1;
  font-weight: 600;
  color: #ffffff;
}

.theme-input-group button:hover {
  background-color: #fb9389;
}

/* Theme Input Style */
.theme-input-style {
  border: none;
  height: 52px;
  width: 100%;
  padding: 0 20px;
  background-color: #F9F9F9;
  font-size: 16px;
}

.theme-input-style:focus {
  background-color: #fffcf6;
}

textarea.theme-input-style {
  height: 200px;
  padding: 20px 20px;
}

/* Others */
.ov-hidden {
  overflow: hidden;
}

.ovx-hidden {
  overflow-x: hidden;
}

/* Pagination*/
.pagination {
  margin-top: 20px;
}

.pagination li {
  margin: 0 10px;
}

.pagination li a,
.pagination li span {
  width: 50px;
  height: 50px;
  line-height: 1;
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: #f6f6f6;
  color: #5E5E5E;
  font-size: 24px;
}

.pagination li a:hover, .pagination li a.active,
.pagination li span:hover,
.pagination li span.active {
  color: #ffffff;
}

.pagination li.nav-btn a.disabled {
  cursor: not-allowed;
  opacity: .5;
}

/* Blockquote */
blockquote {
  padding: 50px 20px;
  position: relative;
  font-size: 24px;
  line-height: 1.33;
  font-weight: 500;
}

blockquote cite {
  display: block;
  margin-top: 15px;
  font-size: 18px;
  line-height: 1.44;
  font-style: normal;
  font-weight: 400;
}

blockquote:after {
  position: absolute;
  content: '“';
  left: 50%;
  top: 50%;
  font-size: 240px;
  font-weight: 700;
  line-height: 41px;
  transform: translate(-50%, -50%);
  opacity: .1;
}

/* Form Response */
.form-response {
  background-color: rgba(0, 33, 122, 0.2);
}

.form-response span {
  display: block;
  margin-top: 20px;
  margin-bottom: 30px;
  padding: 10px 15px;
}

/* Border Effect */
.border-effect, .btn, .single-service {
  position: relative;
  border: 2px solid #e7e7e7 !important;
  z-index: 1;
}

.border-effect:before, .btn:before, .single-service:before, .border-effect:after, .btn:after, .single-service:after {
  position: absolute;
  content: '';
  display: block;
  height: 30px;
  width: 30px;
  transition: .4s ease-in;
  z-index: -1;
}

.border-effect:before, .btn:before, .single-service:before {
  border-bottom: 2px solid;
  border-left: 2px solid;
  left: -2px;
  bottom: -2px;
}

.border-effect:after, .btn:after, .single-service:after {
  border-top: 2px solid;
  border-right: 2px solid;
  right: -2px;
  top: -2px;
}

.border-effect:hover:after, .btn:hover:after, .single-service:hover:after, .border-effect:hover:before, .btn:hover:before, .single-service:hover:before {
  width: calc(100% + 4px);
  height: calc(100% + 2px);
}

/* Title Border */
.title-border {
  font-size: 0;
  line-height: 1;
}

.title-border span {
  display: inline-block;
  height: 3px;
  width: 12px;
}

.title-border span:first-child {
  width: 24px;
}

.title-border span:not(:last-child) {
  margin-right: 3px;
}

/* Bg */
.light-bg, .single-testimonial .single-testimonial-inner:after {
  background-color: #FFFCF6;
}

/* Search Form */
.search-form .theme-input-group input, .widget.widget_search .theme-input-group input, .widget.widget_newsletter.style--two .theme-input-group input {
  background-color: #F9F9F9;
}

.search-form .theme-input-group input::placeholder, .widget.widget_search .theme-input-group input::placeholder, .widget.widget_newsletter.style--two .theme-input-group input::placeholder {
  color: #C9C9C9 !important;
}

.search-form .theme-input-group input:focus, .widget.widget_search .theme-input-group input:focus, .widget.widget_newsletter.style--two .theme-input-group input:focus {
  background-color: #fffcf6;
}

/* ************************
   02.2: Animations
   ********************* */
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -40px, 0);
    transform: translate3d(0, -40px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 40px, 0);
    transform: translate3d(0, 40px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

@keyframes matrix {
  0% {
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
  25% {
    transform: matrix(1, 0, 0, 1, 15, 20);
  }
  50% {
    transform: matrix(1, 0, 0, 1, -5, 25);
  }
  75% {
    transform: matrix(1, 0, 0, 1, -15, 15);
  }
  100% {
    transform: matrix(1, 0, 0, 1, 0, 0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

@-webkit-keyframes slideOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}

@keyframes slideOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}

.slideOutUp {
  -webkit-animation-name: slideOutUp;
  animation-name: slideOutUp;
}

@-webkit-keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.slideInDown {
  -webkit-animation-name: slideInDown;
  animation-name: slideInDown;
}

@keyframes inner-circles-loader {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

/* ************************
   02.3: Preloader
   ********************* */
.preloader {
  z-index: 999999;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
}

.preloader .loader {
  transform: translate3d(0, 0, 0);
  position: relative;
  display: inline-block;
  width: 50px;
  height: 50px;
  background: #FB8579;
  border-radius: 50%;
  overflow: hidden;
  text-indent: -9999px;
  mask-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAIAAACQd1PeAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAA5JREFUeNpiYGBgAAgwAAAEAAGbA+oJAAAAAElFTkSuQmCC);
}

.preloader .loader:before, .preloader .loader:after {
  content: '';
  position: absolute;
  top: 0;
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.preloader .loader:before {
  animation: inner-circles-loader 3s infinite;
  transform-origin: 0 50%;
  left: 0;
  background: #FCADA5;
}

.preloader .loader:after {
  animation: inner-circles-loader 3s 0.2s reverse infinite;
  transform-origin: 100% 50%;
  right: 0;
  background: #FDC8C3;
}

